.chip{
    display: flex;
    gap: 10px;
    align-items: center;
    padding: 4px 8px;
    color: #fff;
    background-color: gray;
    width: fit-content;
    border-radius: 40px;
    font-size: 14px;


}

.chip svg{
    height: 16px;
    width: 16px;
    cursor: pointer;
    
}