.Card{
    padding: 10px;
    border-radius: 5px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    gap:10px;
    cursor: pointer;
}

.card_top{
    display: flex;
    gap: 5px;
    
}

.card_top_labels{
    flex: 1;
    display: flex;
    gap: 10px;
}
.card_top svg{
    opacity: 0;
    transition: 200ms;

}

.Card:hover .card_top svg{
    opacity: 1;
}

.card-title{
    font-weight: bold;
}

.card-footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    
}
.card-footer p{
    display: flex;
    gap: 5px;
    align-items: center;
    
}

.card_footer svg{
    height: 14px;
    width: 14px;

}

.card_top_more{
    position: relative;
    cursor: pointer;
}

.card_dropdown{
    background-color: #fff;
    width: 130px;
    padding: 10px;
    border-radius: 3px;
    box-shadow: 1px 0 20px rgba(0,0,0,0.05);
}
.card_dropdown p{
    cursor: pointer;
}